(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .directive('range', drctv);

  /** @ngInject */
  function drctv() {
    var directive = {
      restrict: 'E',
      templateUrl: 'app/components/range/range.html',
      scope: {
        min: '=',
        max: '=',
        model: '=',
        disabled: '=',
        change: '&'
      },
      link: linkFunc,
      controller: ctrl,
      controllerAs: 'vm',
      bindToController: true
    };

    return directive;

    function linkFunc(scope, el, attr, vm) {
      var watcher;

      var update = _.debounce(vm.change, 500);

      watcher = scope.$watch('vm.model', function () {
        el[0].children[0].style.backgroundSize = ((vm.model - vm.min) / (vm.max - vm.min) * 100) + '% 100%';
        el[0].children[0].value = vm.model;

        update();
      });

      scope.$on('$destroy', function () {
        watcher();
      });
    }

    /** @ngInject */
    function ctrl() {
      var vm = this;

      return vm;
    }
  }

})();

(function() {
  'use strict';

  angular
    .module('coherentPaDemo')
    .factory('githubContributor', githubContributor);

  /** @ngInject */
  function githubContributor($log, $http) {
    var apiHost = 'https://api.github.com/repos/Swiip/generator-gulp-angular';

    var service = {
      apiHost: apiHost,
      getContributors: getContributors
    };

    return service;

    function getContributors(limit) {
      if (!limit) {
        limit = 30;
      }

      return $http.get(apiHost + '/contributors?per_page=' + limit)
        .then(getContributorsComplete)
        .catch(getContributorsFailed);

      function getContributorsComplete(response) {
        return response.data;
      }

      function getContributorsFailed(error) {
        $log.error('XHR Failed for getContributors.\n' + angular.toJson(error.data, true));
      }
    }
  }
})();

